.footer {
    margin-top: 150px;
    width: 100%;
    padding: 0px 10%;
    padding-top:4rem;
    background-color: #f0f0f0;
}
.container-foot{
    display: flex;
    justify-content: space-evenly;
}
.col-1,.col-3{
    flex-direction: column;
}
.footer h3 {
    color: #000;
    font-size: 1.2rem;
    text-align: center;
    margin-bottom: 30px;
}
.footer-links{
    display: flex;
}
.hover{
    display: inline;
    font-size: 1.2rem;
    margin: 10px;
    padding: 5px;
    color: #000;
    cursor: pointer;
}
.hover:hover{
    color: #f1c40f;
}
.address{
    display: block;
    font-size: .9rem;
    color: #000;
}
.email{
  display: block;
  font-size: .9rem;
  color: #000;
  margin-top: 10px;
    
}
.address span{
    font-weight: bold;
    color: #000;
}
.email span{
    font-weight: bold;
    color: #000;
}
.number span{
    color: #000;
    font-weight: bold;
}
.number{
    color: #000;
    display: block;
    font-size: .9rem;
    margin-top: 10px;    
}
.end{
    color: #000;
    display: block;
    font-size: 1.2rem;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 10px;
    padding: 10px;
}
@media (max-width:415px) {
    .container-foot{
       display: block;
    }
    
}