.detail {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 90vh;
}
.left-img {
  display: inline-block;
  height: 430px;
  margin-right: 50px;
  width: 650px;
  margin-top: 100px;
  border: 5px solid #f1c40f;
  border-radius: 50%;
}
.right-content {
  margin-left: 70px;
}
.right-content p {
  font-size: 1.4rem;
  letter-spacing: 1px;
  display: inline-block;
  font-style:oblique;
  padding: 10px;
}
.right-content h1 {
  font-style:oblique;
  display: inline-block;
  font-size: 3rem;
  letter-spacing: 2px;
} 
.left-side{
  animation: left-side 1s linear forwards;
  transition: 2s ease-in-out;
  opacity: 0;
} 
@keyframes left-side {
  0%{
    margin-left: 150px;
    opacity: 0;
  }
  100%{
    margin-left: 0;
    opacity: 1;
  }
  
} 
.right-content span{
  color: #f1c40f;
}
.btn1 {
    margin-left: 3%;
    margin-top: 35px;
    width: 120px;
    height: 30px;
    font-weight: 400;
    border-radius: 30px;
    border: none;
    color: #080808;
    padding: 5px;
    background: #fffc00;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #ffffff, #fffc00);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #ffffff, #fffc00); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    transition: 0.3s;
      overflow-y: hidden;
  }
  
  .btn1:hover {
    transform: scale(1.1);
    cursor: pointer;
  }

  /* RESPONSIVE DESIGN  */

  
@media (max-width: 768px) {
  .detail {
    flex-direction: column-reverse;
    align-items: center;
    text-align: center;
  }

  .left-img {
    width: 80%;
    max-width: 380px;
    margin-top: 20px;
    margin-right: 0;
  }

  .right-content {
    margin: 20px 0;
  }
}
