
nav {
  height: 5.4rem;
  width: 100%;
  background: #fffc00;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #ffffff, #fffc00);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #ffffff, #fffc00);
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo{
  display: flex;
  align-items: center;
}
.menu{
  position: absolute;
  top: 4%;
  right: 0;
  color: #000;
  margin-right: 20px;
  cursor: pointer;
  display: none;
  /* background-color: #ffffff; */
  /* height: 200px;
  width: 200px; */
}
.menu-btn{
  background-color: transparent;
  border: none; 
}
.logo-name {
  font-size: 4rem;
  display: inline;
  font-style:italic;
  margin-left: 20px;
  color: #000;
}
.logo span {
  color: #f1c40f;
  letter-spacing: 1px;
  font-size: 2.3rem;
}
.laptop-screen{
  list-style: none;
  margin-right: 20px;
  overflow:hidden;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
ul li {
  color: #000;
  font-size: 1.2rem;
  margin: 10px;
  padding: 5px;
  cursor: pointer;
  /* display: none; */
}
.hover-line {
  transition: .3s smooth;
}

.hover-line:hover {
  color: #f1c40f;
}
@media (max-width:768px) {
  .mobile-screen{
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 400px;
    width: 300px;
  }
  .hover-line{display: none;}
  .menu{
    display: block;
  }

}