.service {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  height: 100vh;
}

.service-left {
  margin-top: 10%;
}

.service-detail {
  display: block;
  font-size: 1rem;
  padding: 1rem;
}

.box {
  height: 20rem;
  width: 330px;
  border: 2px solid #f1c40f;
  border-radius: 10px;
  text-align: center;
  cursor: pointer;
}

.box img {
  height: 6rem;
  width: 7rem;
  text-align: center;
  margin-top: 1rem;
}

.box-1 {
  margin-top: 50px;
  transition: 0.2s ease-in;
}

.box-1:hover {
  transform: translateX(-30px);
}

.box-2 {
  margin-top: 230px;
  transition: 0.2s ease-out;
}

.box-2:hover {
  transform: translateX(30px);
}

.box-3 {
  margin-top: 30px;
  transition: 0.2s ease-in;
}

.box-3:hover {
  transform: translateX(-30px);
}

.box a {
  text-decoration: none;
  text-align: center;
  color: #080808;
  background: #fffc00;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #ffffff, #fffc00);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #ffffff, #fffc00);
  width: 7rem;
  padding: 2px;
  height: 2rem;
  border-radius: 20px;
  display: inline-block;
  cursor: pointer;
}

.box a:hover {
  background-color: #655309;
}

.title {
  font-size: 3.7rem;
  font-style: italic;
  font-weight: bold;
}

.title-span {
  font-style: italic;
  font-size: 3.7rem;
  color: #f1c40f;
}

.btn {
  margin-left: 3%;
  margin-top: 35px;
  width: 120px;
  height: 30px;
  font-weight: 400;
  border-radius: 30px;
  border: none;
  color: #080808;
  padding: 5px;
  background: #fffc00;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #ffffff, #fffc00);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #ffffff, #fffc00);;
  transition: 0.3s;
}

.btn:hover {
  transform: scale(1.1);
  cursor: pointer;
}

.box h2 {
  font-size: 1.8rem;
  margin-top: 1rem;
}

.box-title {
  font-size: 1rem;
  padding: 1rem;
}

/* RESPONSIVE DESIGN */

@media (max-width: 768px) {
  .service {
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    align-items: center;
    height: auto;
  }
 .service-left{
    text-align: center;
  }
  
  .service-left,
  .service-right {
    margin-top: 3rem;
  }
  
  .box {
    max-width: 330px;
    margin-bottom: 2rem;
    margin-top: 5%;
  }
}

@media (max-width: 415px) {
  .btn{
    margin-bottom: 30px;
  }
}
