.toggle{
    display: flex;
    justify-content: space-between;
    border: 2px solid #f1c40f;
    border-radius: 1rem;
    position: relative;
    padding: 2px;
    cursor: pointer;
    width: 4rem;
    margin-top: 25px;
    margin-left: 7px;
}
.toggle>*{
    width: 1rem;
    height: 1rem;
    color: #f1c40f;
}
.toggle-btn{
    border-radius: 100%;
    background: #f1c40f;
    position: absolute;
}