
.about{
    margin-top: 80px;
}

.img-about{
    border-radius: 15px;
    margin-top: 50px;
  }
  .img-about{
    height: auto;
    width: 430px;
    margin-bottom: 130px;
  }
  
  .about-content {
    width: 550px;
    margin-bottom: 200px;
  }
  
  .main {
    display: flex;
    width: 1130px;
    max-width: 95%;
    margin: 0px auto;
    align-items: center;
    justify-content: space-around;
  }
  
  .about-content h2 {
    font-size: 3.7rem;
    margin: 30px;
    font-style: italic;
    letter-spacing: 1px;
  }
  
  .about-content h5 {
    font-size: 20px;
    letter-spacing: 1px;
    margin: 30px;
  }
  
  .about-content span {
    color: #f1c40f;
  }
  
  .about-content p {
    font-size: 16px;
    letter-spacing: 1px;
    padding: 10px;
    margin: 26px;
  }
  
  .btn-about {
    width: 120px;
    height: 30px;
    margin-left: 29px;
    border-radius: 30px;
    color: #080808;
    border: none;
    font-size: 14px;
    padding: 5px;
    outline: #fff;
background: #fffc00;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #ffffff, #fffc00);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #ffffff, #fffc00); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    transition: 0.3s;
  }
  .btn-about:hover {
    transform: scale(1.2);
    cursor: pointer;
  }
  .container_exp{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 3rem
  }
  .circle{
    border-radius: 50%;
    border:8px solid linear-gradient(to right, #ffffff, #fffc00);
    background: -webkit-linear-gradient(to right, #ffffff, #fffc00);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #ffffff, #fffc00); 
    height: 10rem;
    width: 10rem;
  }
  .circle h4 {
    font-size: 1.2rem;
    text-align: center;
    margin-top: 55px;
    width: auto;
    color: #080808;
  }

  @media (max-width:768px) {
    .main{
      flex-direction: column;
      align-items: center;
    }
    
  }
  @media (max-width:415px){
    .img-about{
      width: 360px;
    }
  .about-content{
    text-align: center;
    margin-bottom: 5%;
  }
  .container_exp {
    flex-direction: column;
    gap: 1rem;
  }
  .about-content p {
    padding: 0px 60px;
  }
}