contact-section{
  margin-top: 150px;
}
.contact{
  display: flex;
 justify-content: space-evenly;
  margin-top: 90px;
}
.contact-info h2{
  font-size: 3.7rem;
  font-style: italic;
  margin-top: 4rem;
}
.contact-h2 {
  font-size: 3rem;
  font-style: italic;
  text-align: center;
  margin-bottom: 20px;
}
form {
  height: 600px;
  width: 500px;
  margin: 18px;
  background-color: rgb(52, 50, 50);
  /* border:20px solid #f1c40f; */
  padding: 20px;
  text-align: center;
  border-radius: 20px;
}
input {
  display: inline-block;
  padding: 5px;
  height: 30px;
  width: 400px;
  margin-top: 10px;
  border: none;
  /* border-radius: 4px; */
}
textarea {
  display: inline-block;
  height: 200px;
  padding: 10px;
  width: 400px;
  /* border-radius: 4px; */
  margin-top: 10px;
  border: none;
}
.btn-msg {
  display: block;
  color: #000;
  text-align: center;
  font-size: 15px;
  background: #fffc00;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #ffffff, #fffc00);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #ffffff, #fffc00);;
  width: 87%;
  height: 50px;
  margin: auto;
  margin-top: 10px;
  transition: .3s;
  border: 1px solid gainsboro;
  cursor: pointer;
}
.btn-msg:hover {
  transform: scale(1.1);
}
.container h5 {
  font-size: 1.5rem;
  color: #f1c40f;
  margin-top: 1rem;
}
.p-2{
  font-size: 1.2rem;
  font-weight: bold;
}
.social-icon{
width: 80%;
display: flex;
align-items: center;
margin-top: 20px;

}
.icons{
color: #f1c40f;
margin-right: 20px;
transition: .3s;
}
.icons:hover{
transform: translateY(-10px);
}
@media(max-width:768px) {
  .contact{
    flex-direction: column;
    align-items: center;
  }
  form{
    margin-top: 50px;
  }
  
}
@media(max-width:415px){
  .container{
    text-align: center;
  }
  .social-icon{

    justify-content: end;
    margin-left: 25px;
  }
  form{
    width: 350px;
  }
  input,textarea{
    width: 300px
  }
  .btn-msg {
    width: 97%;
  }
}