.skills-section{
  height: 90vh;
  margin-left: 10%;
}
.h2 {
  font-size: 3.7rem;
   width: 100%; 
  margin: 30px ;
  text-align: center;
  font-style: italic;
  font-weight: bold;
}

.bar {
  width: 80%;
  height: 66px;
  background: #f0f0f0;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
  padding: 25px;
  margin: 10px;
  overflow: hidden;
}
.skills-section .bar {
  margin: 20px 0;
}
.bar.bar:first-child {
  margin-top: 0px;
}
.bar .info {
  font-size: 17px;
  font-weight: 500;
  margin-bottom: 6px;
}
.info span {
  margin-bottom: 8px;
  color: #000;
}
.bar .progress-line {
  height: 10px;
  width: 100%;
  position: relative;
  background: #f0f0f0;
  box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0, 0.5),
    0px 1px rgba(225, 225, 225, 0.8);
    border-radius: 10px;
}
.bar .progress-line span {
  height: 100%;
  width: 80%;
  background: #fffc00;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #ffffff, #fffc00);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #ffffff, #fffc00);
  position: absolute;
}

.progress-line.html span {
   width: 80%;
 }
 .progress-line.css span {
   width: 80%;
 }
 .progress-line.js span {
   width: 70%;
 }
 .progress-line.bt span {
   width:  80%;
 }
 .progress-line.rt span {
  width:  65%;
}
 .progress-line span::before {
  position: absolute;
  content: "";
  right: 0;
  top: -10px;
  height: 0;
  width: 0;
  border: 7px solid transparent;
  border-bottom-width: 0px;
  border-right-width: 0px;
  border-top-color: #000;
}
 .progress-line span::after {
  position: absolute;
  content: "90%";
  right: 0;
  top: -4px;
  overflow-y: hidden;
  color: #000;
  font-size: 12px;
  font-weight: bold;
  background: #fff;
  padding: 1px 8px;
  border-radius: 3px;
}
.progress-line.html span::after{
  content:'80%';
}
.progress-line.css span::after {
  content:'80%';
}
.progress-line.js span::after {
  content: '75%';
}
.progress-line.bt span::after {
  content: '80%';
}
.progress-line.rt span::after {
  content: '70%';
}


@media (max-width: 768px) {
  .skills-section{
    height: auto;
  }
  .h2 {
     width: 690px; 
}
}

@media (max-width: 415px){
  .h2 {
    width: 0;
    text-align: center; 
}
  skills-section .bar {
    margin: 20px;
  }
}